import { ProfileService } from './../../services/profile.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OrderState } from 'src/app/shared/models/profile.model';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-order-list-detail',
  templateUrl: './order-list-detail.page.html',
  styleUrls: ['./order-list-detail.page.scss'],
})
export class OrderListDetailPage implements OnInit {
  item: any;
  id: number;
  typeDataReward: any;
  loadingContent: boolean;

  environment = environment;
  constructor(
    private route: ActivatedRoute,
    private profileService: ProfileService,
  ) { }

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      this.id = parseInt(params.get('id'));
      this.getReward();
    });
  }

  async getReward() {
    try {
      this.loadingContent = true;
      this.item = await this.profileService.getOrderUser(this.id).toPromise();
      console.log('Order ', this.item);
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  dateToString(date: Date) {
    if (date) {
      return (("0" + (date.getDate())).slice(-2)) + "/" + (("0" + (date.getMonth() + 1)).slice(-2)) + "/" + date.getFullYear() +" "+(("0" + (date.getHours())).slice(-2)) +":"+(("0" + (date.getMinutes())).slice(-2));
    } else return '';
  }

  setDotColorState(state: string): string {
    let color: string = '#bbb';
    switch (state){
      case OrderState.REQUESTED:
        color = '#297fb9';
        break;
      case OrderState.OBTAINED:
        color = '#50c02b';
        break;
      case OrderState.REJECTED:
        color = '#c0392b';
        break;
    }
    return color;
  }
}
